import { Component, OnInit } from '@angular/core';
import{ FormGroup, FormControl, FormsModule, ReactiveFormsModule, Validators} from '@angular/forms';

import 'rxjs/Rx';

@Component({
  selector: 'app-reactive-model-from',
  templateUrl: './reactive-model-from.component.html',
  styleUrls: ['./reactive-model-from.component.css']
})
export class ReactiveModelFromComponent implements OnInit {
  constructor() { }
  searches: string[] = [];
  searchField: FormControl;

  ngOnInit() {
    this.searchField = new FormControl();
    this.searchField.valueChanges
      .subscribe(item => {
        this.searches.push(item);
      });
  }

  reset(){
    this.searches = [];
  }

}



