import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule , ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { LoginButtonComponent } from './header/login-button/login-button.component';
import { BarFooComponent } from './header/login-button/bar-foo/bar-foo.component';
import { MyDirective } from './my.directive';
import { MyPipe } from './my.pipe';
import { JokeComponent } from './joke/joke.component';
import { JokeFormComponent } from './joke-form/joke-form.component';
import { JokeListComponent } from './joke-list/joke-list.component';
import { ShowHideComponent } from './show-hide/show-hide.component';
import { ModelFormComponent } from './model-form/model-form.component';
import { ReactiveModelFromComponent } from './reactive-model-from/reactive-model-from.component';
import { TemplateFormComponent } from './template-form/template-form.component';


@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    LoginButtonComponent,
    BarFooComponent,
    MyDirective,
    MyPipe,
    JokeComponent,
    JokeFormComponent,
    JokeListComponent,
    ShowHideComponent,
    ModelFormComponent,
    ReactiveModelFromComponent,
    TemplateFormComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    ReactiveFormsModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



