export class Joke{
  public setup: string;
  public punchline: string;
  public hide: boolean;

  constructor(setup:string,punchline:string){
    this.setup = setup;
    this.punchline = punchline;
    this.hide =true;
    console.log('i am inside construcor');
  }

  toggle(){
    this.hide = !this.hide;
  }
}



